.d-LoginPage {
  width: 100%;
  max-width: 600px;
  min-height: 600px;
  overflow: hidden;
  margin: auto;
  padding: 20px 0px;
  h2 {
    text-align: center;
    margin: 10px;
    display: block;
  }
  &__imgUserList {
    display: flex;
    justify-content: center;
    flex-wrap: wrap;
    width: 90%;
    margin: auto;
    &--img {
      margin: 10px;
      cursor: pointer;
      img {
        width: 110px;
        height: 110px;
        object-fit: cover;
        border-radius: 100%;
        box-sizing: border-box;
        &:hover {
          border: 3px solid blue;
        }
      }
      &.selected {
        img {
          border: 5px solid red;
        }
      }
    }
  }
  form {
    text-align: center;
    box-shadow: 0px 0px 20px -8px black;
    display: flex;
    justify-content: space-between;
    align-items: center;
    max-width: 400px;
    width: 90vw;
    border-radius: 30px;
    height: 50px;
    margin: auto;
    padding: 0px 10px;
    input {
      width: 300px;
      height: 30px;
      margin-right: 10px;
      border: none;
      outline: none;
    }
    button {
      border: none;
      width: 40px;
      height: 40px;
      border-radius: 100%;
      cursor: pointer;
      outline: none;
      display: flex;
      justify-content: center;
      align-items: center;
      &::before {
        content: '';
        display: block;
        border-right: 2px solid black;
        border-top: 2px solid black;
        width: 10px;
        height: 10px;
        transform: rotate(45deg);
      }
    }
  }
}
